export default function initLatestNewsSlider() {
    const slider = document.querySelector('.news-cards');
    const slides = document.querySelectorAll('.news-card');
    const dots = document.querySelectorAll('.slider-dot');
    const prevButton = document.getElementById('prevSlide');
    const nextButton = document.getElementById('nextSlide');

    let currentSlide = 0;
    let slideWidth = slides[0].offsetWidth + 
    parseInt(window.getComputedStyle(slides[0]).marginLeft) + 
    parseInt(window.getComputedStyle(slides[0]).marginRight);

    console.log('Initial slide width:', slideWidth); // Проверка начальной ширины слайда

    function updateSlider() {
        slider.style.transform = `translateX(-${currentSlide * slideWidth}px)`;

        dots.forEach((dot, index) => {
            dot.classList.toggle('active', index === currentSlide);
        });

            nextButton.style.opacity = currentSlide >= slides.length - 1 ? '0.5' : '1';
            prevButton.style.opacity = currentSlide <= 0 ? '0.5' : '1';

        console.log('Current slide:', currentSlide); // Проверка текущего слайда
    }

    function nextSlide() {
        if (currentSlide < slides.length - 1) {
            currentSlide++;
            updateSlider();
        }
    }

    function prevSlide() {
        if (currentSlide > 0) {
            currentSlide--;
            updateSlider();
        }
    }

    nextButton.addEventListener('click', nextSlide);
    prevButton.addEventListener('click', prevSlide);

    dots.forEach((dot, index) => {
        dot.addEventListener('click', () => {
            currentSlide = index;
            updateSlider();
        });
    });

    window.addEventListener('resize', () => {
        slideWidth = slides[0].offsetWidth + parseInt(window.getComputedStyle(slides[0]).marginRight);
        console.log('Resized slide width:', slideWidth); // Проверка ширины слайда после изменения размера окна
        slider.style.transform = `translateX(-${currentSlide * slideWidth}px)`;
    });

    let touchStartX = 0;
    let touchEndX = 0;

    slider.addEventListener('touchstart', e => {
        touchStartX = e.changedTouches[0].screenX;
    });

    slider.addEventListener('touchend', e => {
        touchEndX = e.changedTouches[0].screenX;
        handleSwipe();
    });

    function handleSwipe() {
        const swipeThreshold = 50;
        const diff = touchStartX - touchEndX;

        if (Math.abs(diff) > swipeThreshold) {
            if (diff > 0) {
                nextSlide();
            } else {
                prevSlide();
            }
        }
    }

    updateSlider();
}
